/* eslint-disable no-unused-vars */
import React, { useState, useEffect } from 'react';
import { useHistory } from 'react-router';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import { useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/styles';
import {
	Button,
	CircularProgress
} from '@material-ui/core';
import { login } from 'src/actions';

import { useWeb3React } from '@web3-react/core';
import { InjectedConnector } from '@web3-react/injected-connector';

import * as Constant from 'src/configs/Constant';

const locale = require('react-redux-i18n').I18n;

const useStyles = makeStyles((theme) => ({
	root: {},
	fields: {
		margin: theme.spacing(-1),
		display: 'flex',
		flexWrap: 'wrap',
		'& > *': {
			flexGrow: 1,
			margin: theme.spacing(1)
		}
	},
	wrapper: {
		position: 'relative'
	},
	submitButton: {
		marginTop: theme.spacing(2),
		width: '100%'
	},
	buttonProgress: {
		color: theme.palette.primary,
		position: 'absolute',
		top: '50%',
		left: '50%',
		marginTop: -5,
		marginLeft: -12,
	}
}));

function LoginForm({ className, ...rest }) {
	const classes = useStyles();
	const history = useHistory();
	const dispatch = useDispatch();

	const {
		chainId, account, activate, active, library
	} = useWeb3React();
	const injectedConnector = new InjectedConnector({
		supportedChainIds: Constant.SUPPORTED_CHAIN_IDS
	});

	const [loading, setLoading] = useState(false);

	//	Functions

	useEffect(() => {
		if (chainId && active && loading) {
			setLoading(false);

			//	Dispatch login
			const payload = {
				id: account,
				chainId,
				role: 'ADMIN'
			};
			dispatch(login(payload));
			history.push('/products');
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [chainId, loading]);

	const handleSubmit = () => {
		setLoading(true);
		activate(injectedConnector);
	};

	return (
		<div
			{...rest}
			className={clsx(classes.root, className)}
			onSubmit={handleSubmit}
		>
			{/* <div className={classes.fields}>
				<TextField
					fullWidth
					label={locale.t('login.email')}
					name="email"
					value={chainId || ''}
					variant="outlined"
				/>
				<TextField
					fullWidth
					label={locale.t('login.password')}
					name="password"
					value={account || ''}
					variant="outlined"
				/>
			</div> */}
			<div className={classes.wrapper}>
				<Button
					className={classes.submitButton}
					color="secondary"
					disabled={loading}
					size="large"
					type="submit"
					onClick={handleSubmit}
					variant="contained"
				>
					{locale.t('login.login')}
				</Button>
				{loading && <CircularProgress size={24} className={classes.buttonProgress} />}
			</div>
		</div>
	);
}

LoginForm.propTypes = {
	className: PropTypes.string
};

export default LoginForm;
